import Invoice from "@/types/Invoice";
import User from "@/types/User";

export interface PaymentItem {
  id: number;
  payment_id: number;
  item_name: string;
  quantity: number;
  price: string; // Consider using number if it's always a numeric value
  created_at: string;
  updated_at: string;
}

export default interface Payment {
  id: number;
  invoice_id: string;////change to Invoice number after getting API
  amount: string;
  user_id: string;
  created_at: string;
  updated_at: string;
  invoice : Invoice;
  user: User;
  payment_date: string;
  buyer_name: string;
  payment_method: string,
  tally_company: string;
  collected_by: string;
  payment_number: string;
}

export const paymentKeyHeadMap: Record<string, { head: string; sortable: boolean }> = {

  id: { head: "ID#", sortable: false, },
  payment_date: { head: "Date", sortable: true, },
  tally_company: { head: "Company Name", sortable: false, },
  collected_by: {head: "Collected By", sortable: false},
  payment_number: { head: "Invoice No.", sortable: true, },//change to Invoice number after getting API
  buyer_name: { head: "Buyer", sortable: true, },
  payment_method: { head: "PaymentMethod", sortable: false, },
  amount: { head: "Amount", sortable: false, },
};

export type paymentHead = (typeof paymentKeyHeadMap)[keyof Payment];
export type PaymentKey = keyof Payment

export const paymentKeys = Object.keys(paymentKeyHeadMap);
export const paymentHeadings = Object.values(paymentKeyHeadMap);

export const getKeyFromHead = (head: string): keyof Payment | undefined => {
  const entry = Object.entries(paymentKeyHeadMap).find(([key, value]) => value.head === head);
  return entry ? entry[0] as keyof Payment : undefined;
};

