import React, { useEffect, useMemo, useState } from "react";
import "./payments-list.scss";
import {
  NavLink,
  Outlet,
  useLocation,
  useNavigate,
  useOutletContext,
} from "react-router-dom";
import { useSetOpenNav } from "../..";
import { ReactComponent as BurgerSvg } from "@/assets/svgs/burger.svg";
import { useGetPaymentsMutation } from "@/state/slices/payments/paymentsApiSlice";
import { useLogoutMutation } from "@/state/slices/authApiSlice";
import Payment from "@/types/Payment";
import { useAppDispatch, useAppSelector } from "@/state/hooks";
import useDebounce from "@/utils/hooks/debounce";
import 'react-date-range/dist/styles.css';
// import 'react-date-range/dist/theme/default.css';
import { DateRange, Range } from 'react-date-range';
import { ReactComponent as Calender } from '@/assets/svgs/calendar-2.svg';
import { ReactComponent as DownloadPm } from '@/assets/svgs/download_payment.svg';
import { ChevronDown } from 'lucide-react';
type ContextType = {
  setSelectedCount: React.Dispatch<React.SetStateAction<number>>;
  searchTxt: string;
  payments: Payment[];
  isLoading: boolean;
  loadMorePayments: (page: number) => void;
};

export default function PaymentsList() {
  const [dateStart, setDateStart] = useState<Range[]>([
    {
      startDate: new Date(),
      endDate: undefined,
      key: 'selection',
    },
  ]);
  const [viewCalender, setViewCalender] = useState(false);
  const tabs = [
    {
      name: "Payments",
      icon: (active: boolean) => {
        return active
          ? "/assets/Icon/Deliveries-blue.svg"
          : "/assets/Icon/Deliveries.svg";
      },
      link: "/dashboard/payments/deliveries",
    },
    // {
    //   name: "Collections",
    //   icon: (active: boolean) => {
    //     return active
    //       ? "/assets/Icon/Collections-blue.svg"
    //       : "/assets/Icon/Collections.svg";
    //   },
    //   link: "/dashboard/payments/collections",
    // },
    // {
    //   name: "Completed",
    //   icon: (active: boolean) => {
    //     return active
    //       ? "/assets/Icon/Completed-blue.svg"
    //       : "/assets/Icon/Completed.svg";
    //   },
    //   link: "/dashboard/payments/completed",
    // },
  ];

  const location = useLocation();
  const pathSegments = location.pathname.split("/");
  const lastSegment = pathSegments[pathSegments.length - 1];

  const [selectedCount, setSelectedCount] = useState<number>(0);
  const [searchTxt, setSearchTxt] = useState("");
  const { setOpenNav } = useSetOpenNav();

  return (
    <div className="payments-list-component">
      <div className="nav-header">
        <div
          onClick={() => setSearchTxt("")}
          className="nav-links-payment d-flex justify-content-between align-items-center w-md-100 g-0  m-0 text-12"
        >
          {tabs.map((tab, index) => (
            <NavLink
              to={tab.link}
              key={index}
              className={"text-decoration-none nav-link-cont"}
            >
              <li className={`navbar-link`}>
                <img
                  src={tab.icon(location.pathname === tab.link)}
                  alt={tab.name}
                />
                <span>{tab.name}</span>
              </li>
            </NavLink>
          ))}
        </div>
        <div className="right-bar">
          <div className="search-bar-input">
            <BurgerSvg className="burger" onClick={() => setOpenNav(true)}/>
            <img src={"/assets/Icon/Search.svg"} alt="User Avatar" className={'search-icon'}/>
            <input
                value={searchTxt}
                onChange={(e) => setSearchTxt(e.target.value)}
                type="text"
                className="search-bar-text"
                placeholder="Search By Name or Payment Number"
            />
          </div>
          <div className="date-range-picker">
            <button
              type="button"
              className="btn btn-light"
              onClick={() => setViewCalender(!viewCalender)}>
              <Calender />
              <div className="payment-drp-btn">
                <span>{dateStart[0].startDate?.toLocaleDateString('default', { month: 'short'})}</span>
                <span>{dateStart[0].startDate?.toLocaleDateString('default', { day: 'numeric'})}</span>
                {dateStart[0].endDate ? <span> - {dateStart[0].endDate?.toLocaleDateString('default', { day: 'numeric'})}</span> : null}
                <span>, {dateStart[0].startDate?.toLocaleDateString('default', { year: 'numeric'})}</span>
              </div>
              <ChevronDown className="fs-6" />
            </button>
            {viewCalender && (
              <div className="drp_popover">
                <DateRange
                  editableDateInputs={true}
                  onChange={(item) => setDateStart([item.selection])}
                  moveRangeOnFirstSelection={false}
                  showMonthAndYearPickers={false}
                  ranges={dateStart}
                />
                <div className="drp-btn-wrap">
                  <button
                    type="button"
                    className="btn btn-today">
                    Today
                  </button>
                </div>
                <hr className="drp-hr" />
                <div className="btn-row">
                  <button
                    type="button"
                    className="btn btn-cancel">
                    Cancel
                  </button>
                  <button
                    type="button"
                    className="btn flex-grow-1 btn-apply">
                    Apply
                  </button>
                </div>
              </div>
            )}
          </div>
          <div className="desktop-only download_btn_wrap">
            <button type="button" className="btn">
              <DownloadPm />
            </button>
          </div>

              <div className="mobile-only">
                      <button className="create-batch assign-button">
                        <span>Download</span>
                      </button>
                </div>

          {/*{lastSegment !== "completed" && (*/}
          {/*    <button className="create-batch ">*/}
          {/*      {selectedCount > 0 ? (*/}
          {/*          <div className="selected-count">*/}
          {/*            <span>{selectedCount}</span>*/}
          {/*          </div>*/}
          {/*      ) : (*/}
          {/*          <img src={"/assets/Icon/Add.svg"} alt="User Avatar"/>*/}
          {/*      )}*/}
          {/*      <span>Create Batch</span>*/}
          {/*    </button>*/}
          {/*)}*/}
        </div>
      </div>
      <div className="payments-list-content">
        <Outlet
            context={{
              setSelectedCount,
              searchTxt,
            }}
        />
      </div>
    </div>
  );
}

export function usePaymentsListState() {
  return useOutletContext<ContextType>();
}
