import "./batch-detail.scss";
import "./components/batchDetailsTable/batch-details-table.scss";
import {NavLink, useLocation, useNavigate, useParams} from "react-router-dom";
import ContentSection from "./components/content-section/ContentSection";
import {ReactComponent as BurgerSvg} from "@/assets/svgs/burger.svg";
import {useGetAssociatesQuery} from '@/state/slices/batchs/batchsApiSlice';
import BatchDetailsTable from "@pages/dashboard/batchs/details/components/batchDetailsTable/BatchDetailsTable";
import React, {
  LegacyRef,
  MutableRefObject,
  RefObject,
  useEffect,
  useRef,
  useState,
} from "react";
import Invoice, {InvoiceItem} from "@/types/Invoice";
import {useGetInvoicesMutation} from "@/state/slices/invoices/invoicesApiSlice";
import {numberWithCommas} from "@/utils/helper";
import useGoBackOrRedirect from "@/utils/hooks/goBackOrRedirect";
import {useAppDispatch, useAppSelector} from "@/state/hooks";
import {useSetOpenNav} from "@pages/dashboard";
import BatchDetails from "@/types/BatchDetails";
import {Container, Row, Col, Button, InputGroup, FormControl} from 'react-bootstrap';
import {ReactComponent as PersonCircle} from '../../../../assets/svgs/Profile-circle.svg'
import InvoiceTable from "@pages/dashboard/invoices/(invoicesList)/components/invoiceTable/InvoiceTable";
import {setInvoices} from "@/state/slices/invoices/invoicesSlice";
import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import {ReactComponent as LeftArrow} from "../../../../assets/svgs/arrow-left.svg";
import {ReactComponent as Search} from "../../../../assets/svgs/Search.svg";
import {useGetBatchsMutation} from "@/state/slices/batchs/batchsApiSlice";
import Batch, {Assosiate} from "@/types/Batch";
import {useAddInvoicesToBatchMutation} from '@/state/slices/batchs/batchsApiSlice';
import {useUpdateAssociateMutation} from '@/state/slices/batchs/batchsApiSlice';
import {setBatchDetails, setBatchs} from "@/state/slices/batchs/batchsSlice";

export default function BatchDetail() {
  const batchsState = useAppSelector((state) => state.batchs);
  const {data: AssosiateData, isLoading: isAssosiateLoading, isError: isAssosiateError} = useGetAssociatesQuery();
  const batchs = batchsState.batchs;
  const params = useParams();
  const batch: Batch = batchs.filter(batch => batch.id.toString() === params.batchNo)[0];
  const batchinvoices = batch?.invoices;
  const page = batchsState.page;
  const [searchTxt, setSearchTxt] = useState("");
  const {setOpenNav} = useSetOpenNav();
  const {invoiceNo} = useParams();
  const invoicesState = useAppSelector((state) => state.invoices);
  const invoices = invoicesState.invoices;
  const navigate = useNavigate();
  const location = useLocation();
  const pathname = location.pathname;
  const segments = pathname.split('/').filter(Boolean);
  const lastSegment = segments[segments.length - 1];
  const [getInvoices, {data, isError, isLoading, error}] =
    useGetInvoicesMutation();
  const [invoiceMode, setInvoiceMode] = useState(false);
  const [updateAssociate] = useUpdateAssociateMutation();
  const [selectedAssociateId, setSelectedAssociateId] = useState<number>(0);
  const [selectedVehicleId, setSelectedVehicleId] = useState<number>(0);

  const [assigned, setAssigned] = useState(false);
  const dispatch = useAppDispatch();
  const [showAssignModal, setShowAssignModal] = useState(false);
  const [loadMoreTrigger, setLoadMoreTrigger] = useState(false);
  const handleShowAssignModal = () => {
    setShowAssignModal(true);
    setAssigned(true);
  }
  const [selectedInvoiceIds, setSelectedInvoiceIds] = useState<number[]>([]);
  const [addInvoicesToBatch] = useAddInvoicesToBatchMutation();

  const handleInvoiceSelectionChange = (selectedIds: number[]) => {
    setSelectedInvoiceIds(selectedIds);
  };
  const handleSubmit = async () => {
    if ((!selectedAssociateId || !selectedVehicleId) && location.pathname.includes("deliveries")) {
      alert('Please select both an associate and a vehicle.');
      return;
    }

    try {
      await updateAssociate({
        id: batch.id, // Assuming this is the batch ID you want to update
        user_id: selectedAssociateId,
        vehicle_id: selectedVehicleId,
      }).unwrap();

      // Optionally, refresh the data or give user feedback here
      setShowAssignModal(false);
      alert('Associate updated successfully');
    } catch (error) {
      console.error('Failed to update associate:', error);
      alert('Failed to update associate');
    }
  };


  const handleSave = async () => {
    if (!selectedInvoiceIds.length) return;
    try {
      await addInvoicesToBatch({id: batch.id, invoices: selectedInvoiceIds}).unwrap();
      const response = await getInvoices({page: 1, keyword: ''}).unwrap();
      const batchListUpdated = response?.data?.filter(invoi => invoi.batch_id === batch.id);
      window.location.reload();
      setInvoiceMode(false);
      // Handle success here
    } catch (error) {
      // Handle error here
    }
  };
  const handleCloseAssignModal = () => setShowAssignModal(false);
  useEffect(() => {
    getInvoices({keyword: invoiceNo});
  }, []);

  //useEffect(() => {
  //if (invoiceNo && invoices) {
  //  const idx = invoices.findIndex((i) => i.invoice_number === invoiceNo);
  //  if (idx === -1) navigate("/");
  //  else setInvoice(invoices[idx]);
  //}
  // }, [invoiceNo, invoices]);

  const scrollTo = (ref: RefObject<HTMLDivElement>) => {
    if (ref?.current) {
      ref.current.scrollIntoView({behavior: "smooth"});
    }
  };


  const tabs = [
    {
      name: invoiceMode ? "Add Invoice for Batch" : "Batch ",
      value: lastSegment,
      icon: (active: boolean) => {
        return '/assets/Icon/left-arrow.svg';
      },
      link: invoiceMode ? "" : "/dashboard/batchs/deliveries",
    }
  ];
  const dividerStyle = {
    height: '100%',
    width: '1px',
    backgroundColor: '#000',
    color: "#000",
    marginLeft: '10px',
    marginRight: '10px',
  };
  const backgroundColorWithOpacity = 'rgba(0, 149, 255, 0.06)';
  const borderColorWithOpacity = 'rgba(0, 149, 255, 0.1)';
  const dividerColor = '#D0D5DD';
  const Toolbar = () => {
    return (
      <>
        <div className='mx-4 mt-3 mb-2 desktop-only' style={{
          backgroundColor: backgroundColorWithOpacity,
          border: `1px solid ${borderColorWithOpacity}`,
          borderRadius: '5px',
          padding: '10px'
        }}>
          <div className="d-flex align-items-center justify-content-between">
            <div className="d-flex flex-row">
                       <span className="d-flex align-items-center justify-content-end">
              <PersonCircle className="me-2"/>
                      <div className="d-flex flex-column p-1">
                          <div className="p-0 text-muted batch-label">Associate</div>
                        {batch?.associate?.name && <div className="p-0 text-nowrap assigned-at batch-value">{batch?.associate?.name}</div>}
                        {!batch?.associate?.name && <div className="batch-value">---</div>}
                      </div>
            </span>
              <span style={{
                width: '1px',
                height: '30px',
                backgroundColor: dividerColor,
                marginLeft: '15px',
                marginRight: '15px'
              }}/>
              <span className="d-flex align-items-center justify-content-end">
              <PersonCircle className="me-2"/>
                      <div>
                          <div className="p-0 text-muted batch-label">Assigned By</div>
                      <div className="batch-value">---</div>
                      </div>
            </span>
            </div>
            <div>
              <div className="d-flex flex-row">
                <button className="mx-2 create-batch " onClick={() => {
                  setInvoiceMode(true)
                }}>
                  <span>Add Invoice</span>
                </button>
              </div>

            </div>
          </div>
        </div>
        <div className='mt-3 mb-2 mobile-only batch-profile'>
          <div className="d-flex flex-column align-items-center justify-content-between">
            <div className="d-flex flex-row justify-content-between batch-title">
            <span className="d-flex align-items-center justify-content-end">
              <PersonCircle className="me-2"/>
                      <div>
                          <div className="block-title">Associate</div>
                      <div className="block-value">{batch?.associate?.name || '---'}</div>
                      </div>
            </span>
              <span style={{
                width: '1px',
                height: '30px',
                backgroundColor: dividerColor,
                marginLeft: '15px',
                marginRight: '15px'
              }}/>
              <span className="d-flex align-items-center">
              <PersonCircle className="me-2"/>
               <span>
                          <div className="block-title">Assigned By</div>
                      <div className="block-value">---</div>
                      </span>
            </span>
            </div>
            <div className="batch-sub-title">
              <div className="d-flex flex-row">

                <div className="input-row">
                  <InputGroup className="input-group">
                    <Button variant="button-white" id="button-addon1">
                      <Search/>
                    </Button>
                    <FormControl
                      style={{border: '0'}}
                      aria-label="Example text with button addon"
                      aria-describedby="basic-addon1"
                      placeholder="Search Invoice Number"
                      value={searchTxt}
                      onChange={(e) => setSearchTxt(e.target.value)}
                    />
                  </InputGroup>

                </div>

                <button className="btn btn-primary" type="button" onClick={() => {
                  setInvoiceMode(true)
                }}
                        style={{whiteSpace: 'nowrap'}}>
                  Add Invoice
                </button>

              </div>

            </div>
          </div>
        </div>
      </>
    )
      ;
  };

  let excludedHeadings;
  if (location?.pathname?.includes('collections')) {
    excludedHeadings = ["Date"];
  }
  else if(!location?.pathname?.includes('collections')){
    excludedHeadings = ["Payment", "Due Amount"];
  }
  return (
    <div className="invoices-list-component">

      <div className="nav-header">
        <div
          onClick={() => setSearchTxt("")}
          className="d-flex justify-content-between align-items-center w-md-100 g-0  m-0 text-12"
        >
          {tabs.map((tab, index) => (
            <NavLink
              to={tab.link}
              key={index}
              className={"text-decoration-none nav-link-cont"}
            >
              <li className={`navbar-link`}>
                <LeftArrow onClick={() => {
                  setInvoiceMode(false);
                }}/>
                <span className="nav-name align-middle p-2">{tab.name} <span
                  className="nav-value">{tab.value}</span></span>
              </li>
            </NavLink>
          ))}
        </div>
        <div className="right-bar">

          {!invoiceMode &&
            <span className="d-flex flex-row mx-2">
                                <div className="search-bar-input mx-3">
                                    <BurgerSvg
                                      className="burger"
                                      onClick={() => setOpenNav(true)}
                                    />
                                    <img
                                      src={'/assets/Icon/Search.svg'}
                                      className={'search-icon'}
                                      alt="Search Icon"
                                    />
                                    <input
                                      value={searchTxt}
                                      onChange={(e) => setSearchTxt(e.target.value)}
                                      type="text"
                                      className="search-bar-text"
                                      placeholder="Search by Name or Invoice Number"
                                    />
                                </div>
              {!batch?.associate?.name &&
                <button className="create-batch assign-button" onClick={() => handleShowAssignModal()}>
                  <span>Assign</span>
                </button>}
              {batch?.associate?.name &&
                <button className="create-batch  me-2 px-4 bg-light text-dark assign-button"
                        onClick={() => handleShowAssignModal()}>
                  <span>Reassign</span>
                </button>}
              <button className="btn btn-secondary ms-2 px-4 create-batch report-button">
              <span className="" onClick={()=>navigate("/dashboard/report/"+batch?.id)}>Report</span>
              </button>
                </span>
          }
          {invoiceMode &&
            <span className="d-flex flex-row mx-2">
              <button className="create-batch  me-2 px-4 bg-light text-dark assign-button"
                      style={{border: "1px solid #D0D5DD"}} onClick={() => setInvoiceMode(false)}>
              <span>Cancel</span>
              </button>
                  <button className="report-button create-batch " onClick={handleSave}>
                <span>Save</span>
              </button>
                </span>
          }

        </div>
      </div>
      {!invoiceMode && <Toolbar/>}
      <div className="invoices-list-content">
        <div className="left-container">
          <div

            className="payments-section"
          >
            {/*{!invoiceMode && <InvoiceTable*/}
            {/*  invoices={batchinvoices ?? []}*/}
            {/*  setInvoices={() => {*/}
            {/*  }}*/}
            {/*  showCheckbox={false}*/}
            {/*  loading={isLoading}*/}
            {/*  loadMoreInvoices={() => setLoadMoreTrigger((i) => !i)}*/}
            {/*/>}*/}
            {!invoiceMode && <BatchDetailsTable
                batchs={batch}
                showCheckbox={false}
                loading={isLoading}
                loadMoreBatchs={() => setLoadMoreTrigger((i) => !i)}
                setBatchs={(i:Batch[])=>dispatch(setBatchs(i))}
                excludedHeadings={excludedHeadings}
            />}
            {invoiceMode && <InvoiceTable
              invoices={invoices ?? []}
              setInvoices={(i) => dispatch(setInvoices(i))}
              showCheckbox={true}
              loading={isLoading}
              onSelectionChange={handleInvoiceSelectionChange}
              loadMoreInvoices={() => setLoadMoreTrigger((i) => !i)}
            />}
            <Modal show={showAssignModal} style={{borderRadius: 12}} onHide={handleCloseAssignModal} centered >
              <Modal.Header closeButton style={{margin: 24, padding: 0, paddingBottom: 16}}>
                <Modal.Title>Assign</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Form>
                  <Form.Group className="mb-3" controlId="assignAssociate">
                    <Form.Label>Associate</Form.Label>
                    <Form.Control className={"form-select"} as="select" defaultValue="Choose Assosiate" style={{color: "#667085"}}
                                  onChange={(e) => setSelectedAssociateId(Number(e.target.value))}>
                      {/* Dynamically load options here */}
                      <option value="" selected hidden >Associate
                      </option>
                      {AssosiateData?.associates?.map(associate => (
                        <option key={associate.id} value={associate.id}>{associate.name + "  " + associate.id}</option>
                      ))}
                      {/* ... other options ... */}
                    </Form.Control>
                  </Form.Group>
                  {!location.pathname.includes("collections") &&
                    <Form.Group className="mb-3" controlId="assignVehicle">
                      <Form.Label style={{}}>Vehicle</Form.Label>
                      <Form.Control  className={"form-select"} as="select" defaultValue="Choose Vehicle" style={{color: "#667085"}}
                                    aria-label="Select Vehicle"
                                    onChange={(e) => setSelectedVehicleId(Number(e.target.value))}>
                        {/* Dynamically load options here */}
                        <option selected hidden style={{color: "#667085"}}>Vehicles</option>
                        {AssosiateData?.vehicles?.map(vehicle => (
                          <option key={vehicle.id}
                                  value={vehicle.id}>{vehicle.type + "  License Plate:" + vehicle.id}</option>
                        ))}

                        {/* ... other options ... */}
                      </Form.Control>
                    </Form.Group>}
                </Form>
              </Modal.Body>
              <Modal.Footer style={{borderTop: 0}}>
                <Button variant="primary" onClick={handleSubmit}>
                  Submit
                </Button>
              </Modal.Footer>
            </Modal>

          </div>

        </div>
      </div>
    </div>
  );
}
