import "../batchs/details/batch-detail.scss";
import "../../dashboard/batchs/details/components/batchDetailsTable/batch-details-table.scss";
import { useLocation, useNavigate, useParams} from "react-router-dom";
import {ReactComponent as BurgerSvg} from "@/assets/svgs/burger.svg";
import {useGetAssociatesQuery} from '@/state/slices/batchs/batchsApiSlice';
import BatchDetailsTable from "@pages/dashboard/batchs/details/components/batchDetailsTable/BatchDetailsTable";
import React, {
    RefObject,
    useEffect,
    useState,
} from "react";
import {useGetInvoicesMutation} from "@/state/slices/invoices/invoicesApiSlice";
import {useAppDispatch, useAppSelector} from "@/state/hooks";
import {useSetOpenNav} from "@pages/dashboard"
import {Container, Row, Col, Button, InputGroup, FormControl} from 'react-bootstrap';
import {ReactComponent as PersonCircle} from '../../../assets/svgs/Profile-circle.svg'
import InvoiceTable from "@pages/dashboard/invoices/(invoicesList)/components/invoiceTable/InvoiceTable";
import {setInvoices} from "@/state/slices/invoices/invoicesSlice";
import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import {ReactComponent as LeftArrow} from "../../../assets/svgs/arrow-left.svg";
import {ReactComponent as Search} from "../../../assets/svgs/Search.svg";
import Batch, {Assosiate} from "@/types/Batch";
import {useAddInvoicesToBatchMutation} from '@/state/slices/batchs/batchsApiSlice';
import {useUpdateAssociateMutation} from '@/state/slices/batchs/batchsApiSlice';
import { useGetBatchsMutation } from '@/state/slices/batchs/batchsApiSlice';
import {numberWithCommas} from "@/utils/helper";
import { ReactComponent as SearchIcon } from '@/assets/svgs/Search.svg';
import { ReactComponent as ProfileCircle } from '@/assets/svgs/Profile-circle.svg';
import { NavLink } from 'react-router-dom';
import {ReactComponent as DownArrow} from "../../../assets/svgs/arrow-down.svg";

export default function Reports() {
    const batchsState = useAppSelector((state) => state.batchs);
    const {data: AssosiateData, isLoading: isAssosiateLoading, isError: isAssosiateError} = useGetAssociatesQuery();
    const [batches, setBatches] = useState<Batch[]>([]);
    const [getBatchs, { }] = useGetBatchsMutation();
    const params = useParams();
    const batch: Batch = batches.filter(batch => batch.id.toString() === params.batchNo)[0];
    const [searchTxt, setSearchTxt] = useState("");
    const {setOpenNav} = useSetOpenNav();
    const {invoiceNo} = useParams();
    const invoicesState = useAppSelector((state) => state.invoices);
    const invoices = invoicesState.invoices;
    const navigate = useNavigate();
    const location = useLocation();
    const pathname = location.pathname;
    const segments = pathname.split('/').filter(Boolean);
    const lastSegment = segments[segments.length - 1];
    const [getInvoices, {data, isError, isLoading, error}] =
        useGetInvoicesMutation();
    const [invoiceMode, setInvoiceMode] = useState(true);
    const [updateAssociate] = useUpdateAssociateMutation();
    const [selectedAssociateId, setSelectedAssociateId] = useState<number | null>(null);
    const [selectedVehicleId, setSelectedVehicleId] = useState<number | null>(null);

    const [assigned, setAssigned] = useState(false);
    const dispatch = useAppDispatch();
    const [showAssignModal, setShowAssignModal] = useState(false);
    const [loadMoreTrigger, setLoadMoreTrigger] = useState(false);
    const handleShowAssignModal = () => {
        setShowAssignModal(true);
        setAssigned(true);
    }
    const [selectedInvoiceIds, setSelectedInvoiceIds] = useState<number[]>([]);
    const [addInvoicesToBatch] = useAddInvoicesToBatchMutation();
    const payments = [
        { method: 'Cash', amount: '16,000.00', count: 2, completed: true },
        { method: 'Online', amount: '16,000.00', count: 7, completed: true },
        // ...other payment methods
    ];

    interface PaymentMethodProps {
        method: string;
        amount: string;
        count: number;
        completed: boolean;
    }
    const PaymentMethod: React.FC<PaymentMethodProps> = ({ method, amount, count, completed }) => {
        const buttonClass = completed ? 'btn-completed' : 'btn-pending';
        return (
            <div className={`payment-method ${buttonClass}`}>
                <span className="payment-method-name">{method}</span>
                <span className="payment-method-amount">{amount}</span>
                <span className="payment-method-count">{count}</span>
                <button className={`payment-method-button ${buttonClass}`}>
                    {completed ? 'Completed' : 'Mark as Pending'}
                </button>
            </div>
        );
    };
    const handleInvoiceSelectionChange = (selectedIds: number[]) => {
        setSelectedInvoiceIds(selectedIds);
    };
    useEffect(() => {
        const fetchBatchs = async () => {
            try {
                const page = 1; // Set your desired page
                const keyword = ''; // Set your desired keyword or make dynamic as needed
                const result = await getBatchs({ page, keyword }).unwrap();
                setBatches(result.data);
            } catch (err) {
                console.error('Failed to fetch batches:', err);
            }
        };

        fetchBatchs();
    }, [getBatchs]);
    const handleSubmit = async () => {
        if (!selectedAssociateId || !selectedVehicleId) {
            alert('Please select both an associate and a vehicle.');
            return;
        }

        try {
            await updateAssociate({
                id: batch.id, // Assuming this is the batch ID you want to update
                user_id: selectedAssociateId,
                vehicle_id: selectedVehicleId,
            }).unwrap();

            // Optionally, refresh the data or give user feedback here
            setShowAssignModal(false);
            alert('Associate updated successfully');
        } catch (error) {
            console.error('Failed to update associate:', error);
            alert('Failed to update associate');
        }
    };


    const handleSave = async () => {
        if (!selectedInvoiceIds.length) return;
        try {
            await addInvoicesToBatch({id: batch.id, invoices: selectedInvoiceIds}).unwrap();
            const response = await getInvoices({page: 1, keyword: ''}).unwrap();
            const batchListUpdated = response?.data?.filter(invoi => invoi.batch_id === batch.id);
            window.location.reload();
            setInvoiceMode(false);
            // Handle success here
        } catch (error) {
            // Handle error here
        }
    };
    const handleCloseAssignModal = () => setShowAssignModal(false);
    useEffect(() => {
        getInvoices({keyword: invoiceNo});
    }, []);

    //useEffect(() => {
    //if (invoiceNo && invoices) {
    //  const idx = invoices.findIndex((i) => i.invoice_number === invoiceNo);
    //  if (idx === -1) navigate("/");
    //  else setInvoice(invoices[idx]);
    //}
    // }, [invoiceNo, invoices]);

    const scrollTo = (ref: RefObject<HTMLDivElement>) => {
        if (ref?.current) {
            ref.current.scrollIntoView({behavior: "smooth"});
        }
    };


    const tabs = [
        {
            name: invoiceMode ? "Report" : "Report ",
            value: batch?.associate?.name + " (" + batch?.id +") for "+ `${numberWithCommas(batch?.amount)}` ,
            icon: (active: boolean) => {
                return '/assets/Icon/left-arrow.svg';
            },
            link: invoiceMode ? "" : "/dashboard/batchs/deliveries",
        }
    ];
    const dividerStyle = {
        height: '100%',
        width: '1px',
        backgroundColor: '#000',
        color: "#000",
        marginLeft: '10px',
        marginRight: '10px',
    };
    const backgroundColorWithOpacity = 'rgba(0, 149, 255, 0.06)';
    const borderColorWithOpacity = 'rgba(0, 149, 255, 0.1)';
    const dividerColor = '#D0D5DD';
    const [expandedId, setExpandedId] = useState<number | null>(null);

    const toggleExpand = (id: number) => {
        setExpandedId(expandedId === id ? null : id);
    };


    return (
        <div className="invoices-list-component">

            <div className="nav-header">
                <div
                    onClick={() => setSearchTxt("")}
                    className="d-flex justify-content-between align-items-center w-md-100 g-0  m-0 text-12"
                >
                    {tabs.map((tab, index) => (
                        <NavLink
                            to={tab.link}
                            key={index}
                            className={"text-decoration-none nav-link-cont"}
                        >
                            <li className={`navbar-link`}>
                                <LeftArrow onClick={() => {
                                    setInvoiceMode(false);
                                }}/>
                                <span className="nav-name align-middle p-2">{tab.name} <span
                                    className="nav-value-report">{tab.value}</span></span>
                            </li>
                        </NavLink>
                    ))}
                </div>
                <div className="right-bar">

                    {!invoiceMode &&
                        <span className="d-flex flex-row mx-2">
                                <div className="search-bar-input mx-3">
                                    <BurgerSvg
                                        className="burger"
                                        onClick={() => setOpenNav(true)}
                                    />
                                    <img
                                        src={'/assets/Icon/Search.svg'}
                                        className={'search-icon'}
                                        alt="Search Icon"
                                    />
                                    <input
                                        value={searchTxt}
                                        onChange={(e) => setSearchTxt(e.target.value)}
                                        type="text"
                                        className="search-bar-text"
                                        placeholder="Search by Name or Invoice Number"
                                    />
                                </div>
                            {!assigned &&
                                <button className="create-batch assign-button" onClick={() => handleShowAssignModal()}>
                                    <span>Assign</span>
                                </button>}
                            {assigned &&
                                <button className="create-batch  me-2 px-4 bg-light text-dark assign-button"
                                        onClick={() => handleShowAssignModal()}>
                                    <span>Reassign</span>
                                </button>}
                            <button className="btn btn-secondary ms-2 px-4 create-batch report-button">
              <span className="">Report</span>
              </button>
                </span>
                    }
                    {invoiceMode &&
                        <span className="d-flex flex-row mx-2">
              <button className="create-batch  me-2 px-4 bg-light text-dark assign-button"
                      style={{border: "1px solid #D0D5DD"}} onClick={() => setInvoiceMode(false)}>
              <span>Cancel</span>
              </button>
                  <button className="report-button create-batch " onClick={handleSave}>
                <span>Save</span>
              </button>
                </span>
                    }

                </div>
            </div>

            <div className="invoices-list-content">
                <div className="batchs-list-content">
                    <div className="payment-list-container">
                        {batch?.invoices?.map((method) => (
                            <div key={method.id} className="payment-method">
                                <div className="payment-method-header" onClick={() => toggleExpand(method.id)}>
                                    <div className="payment-method-info">
                                        <span>Cash</span>
                                        <span>{method?.invoice_amount}</span>
                                        <span>{method?.items?.length}</span>
                                    </div>
                                    <DownArrow className={expandedId === method.id ? 'rotated' : ''}/>
                                </div>

                                    <div className="payment-method-details">
                                        {/* Render your payment details here */}
                                        {method?.items?.map((item) => (
                                            <div key={item?.id} className="payment-detail">
                                                <span>Invoice No: {item?.invoice_id}</span>
                                                <span>Amount: {parseInt(item?.price)*(item?.quantity)}</span>
                                            </div>
                                        ))}
                                    </div>

                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}
