import "./batch-details-table.scss";
import React, {useState, useEffect, useMemo, FormHTMLAttributes} from "react";
import {Link, useNavigate} from "react-router-dom";
import {useBatchsListState} from "../../../(batchsList)/layout";
import Batch, {
  batchDetailsHead,
  getKeyFromHead,
  batchDetailsHeadings,
} from "@/types/BatchDetails";
import LoadingTd from "@/components/LoadingTd";
import {SortingDir} from "@/utils/enums";
import BatchDetailsTableTh from "@pages/dashboard/batchs/details/components/BatchDetailsTableTh";
import {formatDate, numberWithCommas} from "@/utils/helper";
import {useAppSelector} from "@/state/hooks";
import Button from 'react-bootstrap/Button';
import {ProgressBar} from "react-bootstrap";
import BatchDetails, {batchDetailsKeyHeadMap} from "@/types/BatchDetails";
import { ReactComponent as CircleRed } from '@/assets/svgs/delete-circle.svg';
import {
  BatchAvatar, BatchAvatarMobile,
  BatchProgress,
  BatchStatus
} from "@pages/dashboard/batchs/(batchsList)/components/batchTable/BatchUtils";
import Invoice from "@/types/Invoice";
import {ReactComponent as Remove} from "../../../../../../assets/svgs/remove.svg";
import Modal from "react-bootstrap/Modal";
import {useDeleteInvoiceFromBatchMutation, useGetBatchsMutation} from "@/state/slices/batchs/batchsApiSlice";
import {useGetPaymentsMutation} from "@/state/slices/payments/paymentsApiSlice";
export type SortingState = Record<
  batchDetailsHead["head"],
  {
    dir: SortingDir;
    optionsOpen: boolean;
  }
>;

export default function BatchDetailsTable({
                                     batchs,
                                     showCheckbox,
                                     setBatchs,
                                     loading = false,
                                     loadMoreBatchs,
                                     excludedHeadings = ["Due", "Overdue By"],
                                   }: {
  batchs: Batch;
  showCheckbox?: boolean;
  setBatchs: (batchs: Batch[]) => void;
  loading?: boolean;
  loadMoreBatchs?: (page: number) => void;
  excludedHeadings?: batchDetailsHead["head"][];
}) {
  const batchsState = useAppSelector((state) => state.batchs);
  const page = batchsState.page;
  const [selectedInvoiceId, setSelectedInvoiceId] = useState<number | null>(null);
  const [selectedInvoice, setSelectedInvoice] = useState<Invoice | null>(null);

  // state for saving current state of batchs after sorting
  // const [sortedBatchs, setSortedBatchs] = useState(batchs);
  const navigate = useNavigate();

  // useEffect(() => {
  //   setSelectedCount(batchs.filter((i) => i.checked).length);
  //   // setSortedBatchs(batchs);
  // }, [batchs]);

  // State for saving sorting states of table headigns
  const [sortingStates, setSortingStates] = useState<SortingState>({});
  const [showEdit, setShowEdit] = useState(false);

  const handleCloseEdit = () => setShowEdit(false);
  const handleShowEdit = () => setShowEdit(true);
  const [showDelete, setShowDelete] = useState(false);

  const handleCloseDelete = () => setShowDelete(false);
  const handleShowDelete = (invoiceId:number, invoice: Invoice) => {
    setSelectedInvoiceId(invoiceId); // Set the currently selected invoice ID
    setSelectedInvoice(invoice); // Set the currently selected invoice ID
    setShowDelete(true); // Show the modal
  };
  const [invoices, setInvoices] = useState(batchs?.invoices);
  const [sortStack, setSortStack] = useState<batchDetailsHead["head"][]>([]);
  const moveToTop = (value: batchDetailsHead["head"]) => {
    setSortStack((prevStack) => {
      const filteredStack = prevStack.filter((item) => item !== value);
      return [value, ...filteredStack];
    });
  };
  const [getBatchs, { data, isError, isLoading, error }] =
      useGetBatchsMutation();
  const [deleteInvoiceFromBatch] = useDeleteInvoiceFromBatchMutation();
  const handleDeleteInvoices = async (invoiceId:number) => {
    handleCloseDelete();
    loading=true;
    try {
      await deleteInvoiceFromBatch({ batchId:batchs.id, invoiceIds: [invoiceId] }).unwrap();
      const response = await getBatchs({ page: 1, keyword: '' }).unwrap();
      // Assuming the response is in the expected format for setPayments
      (setBatchs(response?.data));
     // setInvoices(prevInvoices => prevInvoices?.filter(invoice => invoice?.id !== invoiceId));
      // Handle success, e.g., show a success message
      loading=false;
    } catch (err) {
      // Handle error, e.g., show an error message
    }
  };
  // const detailLink = (batch: BatchDetails): string => {
  //   return `/dashboard/batchs/${batch.id}`;
  // };
  // Getting state coming from parent <Outlet />
  const {setSelectedCount} = useBatchsListState();

  // Creating headings from batchHeadings excluding excludedHeadings
  const headings = useMemo(() => {
    return batchDetailsHeadings.filter(
      (heading) =>
        !excludedHeadings.includes(heading.head) && heading.head !== ""
    );
  }, [excludedHeadings]);

  // Initializing Sorting States such that all headings are asc and options closed
  useEffect(() => {
    if (headings.length && !Object.keys(sortingStates).length) {
      const newSortingStates: typeof sortingStates = {};
      const newSortStack: typeof sortStack = [];
      headings.forEach((h) => {
        if (h.sortable) {
          newSortingStates[h.head] = {
            dir: SortingDir.ASC,
            optionsOpen: false,
          };
          newSortStack.push(h.head);
        }
      });
      setSortingStates(newSortingStates);
      setSortStack(newSortStack);
    }
  }, [headings]);

  // Sorting Batchs whenever sortingStates changes
  //useEffect(() => {
  //   const sortData = (a: BatchDetails, b: BatchDetails): number => {
  //     for (let i = 0; i < sortStack.length; i++) {
  //       const prop = getKeyFromHead(sortStack[i]);
  //       if (prop) {
  //         const heading = batchDetailsKeyHeadMap[prop].head;
  //         const aVal = a[prop] || "";
  //         const bVal = b[prop] || "";
  //         const asc = sortingStates[heading]?.dir === SortingDir.ASC;
  //         if (heading !== "" && sortingStates[heading]) {
  //           if (aVal > bVal) return asc ? 1 : -1;
  //           else if (aVal < bVal) return asc ? -1 : 1;
  //         }
  //       }
  //     }
  //     return 0;
  //   };
  //   setBatchs([...batchs].sort(sortData));
  // }, [sortingStates]);

  const [selectAllChecked, setSelectAllChecked] = useState(false);
  const handleSelectAllCheckbox = (e: React.ChangeEvent<HTMLInputElement>) => {
    const checked = e.target.checked;
    // setSelectAllChecked(checked);
    // const updatedBatchs = batchs.map((batch) => ({
    //   ...batch,
    //   checked: checked,
    // }));

   // setBatchs(updatedBatchs);
    // setSortedBatchs(updatedSortedBatchs);
  };


  useEffect(() => {
    const handleScroll = () => {
      // Check if the user has reached the bottom of the page
      const isAtBottom =
        window.innerHeight + window.scrollY >= document.body.offsetHeight;

      if (isAtBottom && loadMoreBatchs) {
        loadMoreBatchs(page + 1);
      }
    };
    // Attach the event listener to the scroll event
    window.addEventListener("scroll", handleScroll);

    // Clean up the event listener when the component unmounts
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [page]);

  const getVariant = (status: string) => {
    switch (status) {
      case 'Ongoing':
        return 'warning';
      case 'Completed':
        return 'success';
      default:
        return 'secondary';
    }
  };

  return (
    <>
      <table className="batch-table-component">
        <thead>
        <tr>

          {headings.map((heading) => (
            <BatchDetailsTableTh
              key={heading.head}
              heading={heading}
              sortingStates={sortingStates}
              setSortingStates={setSortingStates}
              moveToTop={moveToTop}
              withSorting={showCheckbox}
            />
          ))}
        </tr>
        </thead>
        <tbody >
        {batchs?.invoices?.map((invoice: Invoice, index:number) => (
            <tr key={index} className="link-wrapper">
              {/*// onClick={() => navigate(detailLink("1"))}>*/}
              <div className="link-wrapper desktop-only">
                <td>{invoice?.invoice_number}</td>
                <td>{invoice?.invoice_date}</td>
                <td>{invoice?.buyer_name}</td>
                <td>{invoice?.invoice_amount}</td>
                <td>
                  <BatchStatus batchStatus={"Pending"}/>
                </td>
                <td>
                  <Remove onClick={()=>handleShowDelete(invoice?.id, invoice)}/>
                </td>
              </div>
              <div className="link-wrapper mobile-only payment-row" style={{position: 'relative'}}>
                <div className="title-area" style={{paddingRight: 40}}>
                  <div className="info">
                    <div className="d-flex flex-column align-items-stretch">
                      <div className="p-1 fw-medium" style={{color:"#0080FC"}}>
                        {invoice.id}
                      </div>
                      <div className="d-flex flex-column p-1">
                            <p className="fw-light">{invoice?.buyer_name}</p>
                      </div>
                    </div>
                  </div>
                  <div className="btns">
                    <span className="">
                      <span className="edit-btn">
                        <BatchStatus batchStatus={"Delivered"}/>
                      </span>
                      <span className="delete-btn mobile-delete-button">
                        <Remove onClick={()=>handleShowDelete(invoice.id, invoice)}/>
                        </span>
                    </span>
                  </div>
                </div>

                <div className="payment-dcc">
                  <p>
                    <span className="block-title">Amount</span>
                    <span className="block-value">{numberWithCommas(invoice?.invoice_amount)}</span>
                  </p>
                  <hr/>
                  <p>
                    <span className="block-title">Date</span>
                    <span className="block-value">{invoice?.invoice_date}</span>
                  </p>

                </div>
              </div>

            </tr>

        ))}
        {loading && (
            <tr className="loading-row">
              <LoadingTd cols={headings.length + (showCheckbox ? 1 : 0)} />
            </tr>
        )}
        </tbody>
      </table>
      <Modal show={showDelete} style={{borderRadius: 12}} onHide={handleCloseDelete} centered>
        <Modal.Header  style={{borderBottom: "0px"}}>
          <Modal.Title style={{width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
            <div style={{display: 'flex', alignItems: 'center', flexDirection: 'column', textAlign: "center"}}>
              <CircleRed/>
              <span style={{marginLeft: '10px'}}>You are about to remove</span>
              <span style={{marginLeft: '10px'}}>{selectedInvoice?.invoice_number} - {selectedInvoice?.buyer_name}</span>
              <span style={{marginLeft: '10px'}}>Are you sure you wish to delete? </span>
            </div>

          </Modal.Title>
        </Modal.Header>
        <Modal.Body></Modal.Body>
        <Modal.Footer className={"delete-modal-footer"} >
          <button className={"btn btn-secondary ms-2 px-4 cancel-delete-button"} onClick={handleCloseDelete}>
            <span style={{fontWeight: 600, fontSize: 14}}>NO</span>
          </button>
          <Button variant="primary" className={'conform-delete-button'} onClick={() => selectedInvoiceId && handleDeleteInvoices(selectedInvoiceId)}>
            Yes
          </Button>

        </Modal.Footer>
      </Modal>
      {/* Modals for Edit and Delete actions */}
      {/* ... */}

    </>
  );
}
