import Invoice from "@/types/Invoice";

export interface Vehicle {
    id: number;
    name: number;
    type: string;
    created_at: string;
    updated_at: string;
}
export interface AssociatesAndVehicles {
    associates: Assosiate[];
    vehicles: Vehicle[];
}
export interface Assosiate {
    id: number;
    name: string;
    phone: string;
    email: string;
    email_verified_at: number;
    role: string;
    created_at: string;
    updated_at: string;
}

export default interface Batch {
    id: number;
    name: string;
    time: string;
    avatar: string;
    vehicle: Vehicle;
    invoices: Invoice[];
    associate: Assosiate;
    amount: string;
    status: string;
    totalCount: number;
    completedCount: number;
    payment?: number;
    due_amount?: number;
}

export const batchKeyHeadMap: Record<string, { head: string; sortable: boolean }> = {
    batches: { head: "Batches", sortable: true },
    vehicleType: { head: "Vehicle type", sortable: false },
    amount: { head: "Amount", sortable: false },
    status: { head: "Status", sortable: false },
    progress: { head: "Progress", sortable: false },
};

export type batchHead = (typeof batchKeyHeadMap)[keyof Batch];
export type BatchKey = keyof Batch

export const batchKeys = Object.keys(batchKeyHeadMap);
export const batchHeadings = Object.values(batchKeyHeadMap);

export const getKeyFromHead = (head: string): keyof Batch | undefined => {
    const entry = Object.entries(batchKeyHeadMap).find(([key, value]) => value.head === head);
    return entry ? entry[0] as keyof Batch : undefined;
};

